export default {
  data() {
    return {
      showFlag: false,
      userList: [],
      selectItem: 0,
    }
  },
  props: {
    userType: {
      Number,
      default: 0
    }
  },
  created() {
    //获取localStorage
    this.userList = JSON.parse(localStorage.getItem('USERLIST'));
    // 默认显示第一个
    this.$emit('userData',this.userList.list[0]);
  },
  methods: {
    chooseUser(user,index) {
      this.$emit('userData', user);
      this.selectItem = index;
      this.setShowFlag();
    },
    setShowFlag() {
      this.showFlag = !this.showFlag;
    }
  }
}



