import userList from '@/components/userList';
export default {
  data() {
    return {
      userId: '',
      userType: 30,
    }
  },
  created() {
    // //获取localStorage
    this.userList = JSON.parse(localStorage.getItem('USERLIST'));
    this.userId = this.userList.list[0].parentId;
  },
  methods: {
    getUserList() {
      this.$refs.userList.setShowFlag();
    },
    getData(data) {
      this.userId = data.parentId;
    },
  },
  components: {
    userList
  }
};



